import { useEffect, useMemo, useState } from "react";
import {
  Card,
  Grid,
  Stack,
  Typography,
  TextField,
  Divider,
  MenuItem,
  Autocomplete,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "../../../../redux/store";
import {
  getCountries,
  getStatesByCountry,
  getCitiesByCountry,
  getCitiesByState,
  getGenders,
  getTypeDocs,
} from "../../../../redux/slices/remesas";
import { updateTicketBeneficiary } from "../../../../redux/slices/ticket";

const getCountryCode = (c) =>
  String(c?.code || c?.iso_code || c?.country_code || c?.value || c?.id || "");

const getCountryName = (c) => c?.name || c?.label || c?.description || "";

const nationalityToOption = (countries, code) => {
  const val = String(code || "");
  return countries.find((c) => getCountryCode(c) === val) || null;
};

const doc_countryToOption = nationalityToOption;

const genderCode = (g) => String(g?.value || g?.code || g?.key || "");
const genderLabel = (g) =>
  g?.label || g?.name || g?.description || genderCode(g);

const docCode = (d) => String(d?.value || d?.code || d?.key || "");
const docLabel = (d) => d?.label || d?.name || d?.description || docCode(d);

const stateId = (s) =>
  String(s?.id || s?.value || s?.code || s?.state_id || "");
const stateLabel = (s) =>
  s?.name || s?.label || s?.description || s?.state_name || "";

const cityStateId = (c) =>
  String(c?.state_id || c?.stateId || c?.state || c?.state_code || "");

const cityId = (c) => String(c?.id || c?.value || c?.code || c?.city_id || "");
const cityLabel = (c) =>
  c?.name || c?.label || c?.description || c?.city_name || "";

// ----------------------------------------------------------------------

export default function Ticket({ ticket, onUpdated }) {
  const { t } = useTranslation();
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();

  const { countries, statesByCountry, citiesByCountry, genders, typeDocs } =
    useSelector((state) => state.remesas);

  const [saving, setSaving] = useState(false);

  const [form, setForm] = useState({
    beneficiary_first_name: "",
    beneficiary_middle_name: "",
    beneficiary_last_name: "",
    beneficiary_maiden_name: "",
    beneficiary_phone: "",
    beneficiary_email: "",
    beneficiary_date_of_birth: "",
    beneficiary_activity: "",
    beneficiary_profession: "",
    beneficiary_position: "",
    beneficiary_marital_status: "",
    beneficiary_gender: "",
    beneficiary_nationality: "",
    beneficiary_addr_state_id: "",
    beneficiary_address: "",
    beneficiary_city_id: "",
    beneficiary_doc_type: "",
    beneficiary_doc_number: "",
    beneficiary_doc_issued_date: "",
    beneficiary_doc_due_date: "",
    beneficiary_doc_country: "",
  });

  const genderOptions = useMemo(
    () =>
      (genders || []).map((g) => ({
        value: genderCode(g),
        label: genderLabel(g),
      })),
    [genders]
  );

  const docTypeOptions = useMemo(
    () =>
      (typeDocs || []).map((d) => ({
        value: docCode(d),
        label: docLabel(d),
      })),
    [typeDocs]
  );

  const beneStates = useMemo(
    () => statesByCountry[form.beneficiary_nationality] || [],
    [statesByCountry, form.beneficiary_nationality]
  );

  const beneCities = useMemo(
    () => citiesByCountry[form.beneficiary_nationality] || [],
    [citiesByCountry, form.beneficiary_nationality]
  );

  useEffect(() => {
    dispatch(getCountries());
    dispatch(getGenders());
    dispatch(getTypeDocs());
  }, [dispatch]);

  useEffect(() => {
    if (!form.beneficiary_nationality) return;
    dispatch(getStatesByCountry(form.beneficiary_nationality));
    dispatch(getCitiesByCountry(form.beneficiary_nationality));
  }, [dispatch, form.beneficiary_nationality]);

  useEffect(() => {
    if (!form.beneficiary_addr_state_id) return;
    dispatch(getCitiesByState(form.beneficiary_addr_state_id));
  }, [dispatch, form.beneficiary_addr_state_id]);

  useEffect(() => {
    if (!ticket) return;

    const nat = String(ticket.beneficiary_nationality || "");
    const stId = String(ticket.beneficiary_addr_state_id || "");
    const cId = String(ticket.beneficiary_city_id || "");
    const docCountry = String(ticket.beneficiary_doc_country || "");

    setForm({
      beneficiary_first_name: ticket.beneficiary_first_name || "",
      beneficiary_middle_name: ticket.beneficiary_middle_name || "",
      beneficiary_last_name: ticket.beneficiary_last_name || "",
      beneficiary_maiden_name: ticket.beneficiary_maiden_name || "",
      beneficiary_phone: ticket.beneficiary_phone || "",
      beneficiary_email: ticket.beneficiary_email || "",
      beneficiary_date_of_birth: ticket.beneficiary_date_of_birth || "",
      beneficiary_activity: ticket.beneficiary_activity || "",
      beneficiary_profession: ticket.beneficiary_profession || "",
      beneficiary_position: ticket.beneficiary_position || "",
      beneficiary_marital_status: ticket.beneficiary_marital_status || "",
      beneficiary_gender: ticket.beneficiary_gender || "",
      beneficiary_nationality: nat,
      beneficiary_addr_state_id: stId,
      beneficiary_address: ticket.beneficiary_address || "",
      beneficiary_city_id: cId,
      beneficiary_doc_type: ticket.beneficiary_doc_type || "",
      beneficiary_doc_number: ticket.beneficiary_doc_number || "",
      beneficiary_doc_issued_date: ticket.beneficiary_doc_issued_date || "",
      beneficiary_doc_due_date: ticket.beneficiary_doc_due_date || "",
      beneficiary_doc_country: docCountry,
    });

    if (nat) {
      dispatch(getStatesByCountry(nat));
      dispatch(getCitiesByCountry(nat));
    }
    if (stId) {
      dispatch(getCitiesByState(stId));
    }
  }, [ticket, dispatch]);

  const handleChange = (field) => (event) => {
    setForm((prev) => ({
      ...prev,
      [field]: event.target.value,
    }));
  };
  function clean(obj) {
    const out = {};
    for (const k in obj) {
      const v = obj[k];
      if (v !== null && v !== "" && v !== undefined) out[k] = v;
    }
    return out;
  }

  const handleSubmit = async () => {
    if (!ticket?.id) return;
    setSaving(true);

    try {
      const stateOpt =
        beneStates.find(
          (s) => stateId(s) === String(form.beneficiary_addr_state_id || "")
        ) || null;

      const cityOpt =
        beneCities.find(
          (c) => cityId(c) === String(form.beneficiary_city_id || "")
        ) || null;

      let payload = {
        beneficiary_first_name: form.beneficiary_first_name,
        beneficiary_middle_name: form.beneficiary_middle_name,
        beneficiary_last_name: form.beneficiary_last_name,
        beneficiary_maiden_name: form.beneficiary_maiden_name,
        beneficiary_phone: form.beneficiary_phone,
        beneficiary_email: form.beneficiary_email,
        beneficiary_date_of_birth: form.beneficiary_date_of_birth,
        beneficiary_activity: form.beneficiary_activity,
        beneficiary_profession: form.beneficiary_profession,
        beneficiary_position: form.beneficiary_position,
        beneficiary_marital_status: form.beneficiary_marital_status,
        beneficiary_gender: form.beneficiary_gender,

        beneficiary_nationality: form.beneficiary_nationality,

        beneficiary_address: form.beneficiary_address,

        ...(stateOpt && {
          beneficiary_addr_state: stateLabel(stateOpt),
        }),
        ...(cityOpt && {
          beneficiary_city: cityLabel(cityOpt),
        }),

        beneficiary_doc_type: form.beneficiary_doc_type,
        beneficiary_doc_number: form.beneficiary_doc_number,
        beneficiary_doc_issued_date: form.beneficiary_doc_issued_date,
        beneficiary_doc_due_date: form.beneficiary_doc_due_date,
        beneficiary_doc_country: form.beneficiary_doc_country,
      };

      payload = clean(payload);

      const updated = await updateTicketBeneficiary(
        ticket.transaction_id,
        payload
      );

      enqueueSnackbar(
        t("success.ticket_updated", "Ticket updated successfully"),
        { variant: "success" }
      );

      if (onUpdated) onUpdated(updated);
    } catch (error) {
      console.error(error);
      enqueueSnackbar(t("error.ticket_update_failed", "Ticket update failed"), {
        variant: "error",
      });
    } finally {
      setSaving(false);
    }
  };

  if (!ticket) return null;

  return (
    <Stack spacing={3}>
      <Card sx={{ p: 3 }}>
        <Typography variant="h6" sx={{ mb: 2 }}>
          {t("p_ticket.transaction_info", "Transaction information")}
        </Typography>

        <Grid container spacing={2}>
          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label="Transaction ID"
              value={ticket.transaction_id || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label="Ref code"
              value={ticket.ref_code || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.status", "Status")}
              value={t(`remesas.status_transac.${ticket.transaction_status}`)}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.amount", "Amount")}
              value={ticket.amount || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.currency", "Currency")}
              value={ticket.currency || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.created_on", "Created on")}
              value={ticket.created_on || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>
        </Grid>
      </Card>

      <Card sx={{ p: 3 }}>
        <Typography variant="h6" sx={{ mb: 2 }}>
          {t("p_ticket.sender_info", "Sender information")}
        </Typography>

        <Grid container spacing={2}>
          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.full_name", "Full name")}
              value={
                [
                  ticket.sender_first_name,
                  ticket.sender_middle_name,
                  ticket.sender_last_name,
                ]
                  .filter(Boolean)
                  .join(" ") || ""
              }
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.phone", "Phone")}
              value={ticket.sender_phone || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.email", "Email")}
              value={ticket.sender_email || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("field.city", "City")}
              value={ticket.sender_city || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>

          <Grid item xs={12} md={8}>
            <TextField
              fullWidth
              label={t("field.address", "Address")}
              value={ticket.sender_address || ""}
              InputProps={{ readOnly: true }}
            />
          </Grid>
        </Grid>
      </Card>

      <Card sx={{ p: 3 }}>
        <Stack
          direction="row"
          alignItems="center"
          justifyContent="space-between"
          sx={{ mb: 2 }}
        >
          <Typography variant="h6">
            {t("p_ticket.beneficiary_info", "Beneficiary information")}
          </Typography>
        </Stack>

        <Grid container spacing={2}>
          <Grid item xs={12} md={3}>
            <TextField
              fullWidth
              label={t("remesas.form.firstName")}
              value={form.beneficiary_first_name}
              onChange={handleChange("beneficiary_first_name")}
            />
          </Grid>

          <Grid item xs={12} md={3}>
            <TextField
              fullWidth
              label={t("remesas.form.middleName")}
              value={form.beneficiary_middle_name}
              onChange={handleChange("beneficiary_middle_name")}
            />
          </Grid>

          <Grid item xs={12} md={3}>
            <TextField
              fullWidth
              label={t("remesas.form.lastName")}
              value={form.beneficiary_last_name}
              onChange={handleChange("beneficiary_last_name")}
            />
          </Grid>

          <Grid item xs={12} md={3}>
            <TextField
              fullWidth
              label={t("remesas.form.maidenName")}
              value={form.beneficiary_maiden_name}
              onChange={handleChange("beneficiary_maiden_name")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("remesas.form.phone")}
              value={form.beneficiary_phone}
              onChange={handleChange("beneficiary_phone")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("remesas.form.email")}
              value={form.beneficiary_email}
              onChange={handleChange("beneficiary_email")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              type="date"
              label={t("remesas.form.birthDate")}
              InputLabelProps={{ shrink: true }}
              value={form.beneficiary_date_of_birth || ""}
              onChange={handleChange("beneficiary_date_of_birth")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              select
              fullWidth
              label={t("remesas.form.gender")}
              value={form.beneficiary_gender}
              onChange={handleChange("beneficiary_gender")}
            >
              {genderOptions.map((g) => (
                <MenuItem key={g.value} value={g.value}>
                  {t(`remesas.enums.gender.${g.value}`)}
                </MenuItem>
              ))}
            </TextField>
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("remesas.form.activity")}
              value={form.beneficiary_activity}
              onChange={handleChange("beneficiary_activity")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("remesas.form.profession")}
              value={form.beneficiary_profession}
              onChange={handleChange("beneficiary_profession")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <Autocomplete
              fullWidth
              options={countries}
              value={nationalityToOption(
                countries,
                form.beneficiary_nationality
              )}
              onChange={(_, val) =>
                setForm((prev) => ({
                  ...prev,
                  beneficiary_nationality: val ? getCountryCode(val) : "",
                  beneficiary_addr_state_id: "",
                  beneficiary_city_id: "",
                }))
              }
              getOptionLabel={(o) => (o ? getCountryName(o) : "")}
              renderInput={(params) => (
                <TextField {...params} label={t("remesas.form.nationality")} />
              )}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              select
              fullWidth
              label={t("remesas.common.state")}
              value={form.beneficiary_addr_state_id}
              onChange={(e) => {
                const id = String(e.target.value || "");
                setForm((prev) => ({
                  ...prev,
                  beneficiary_addr_state_id: id,
                  beneficiary_city_id: "",
                }));
              }}
              disabled={!form.beneficiary_nationality}
            >
              {beneStates.map((s) => (
                <MenuItem key={stateId(s)} value={stateId(s)}>
                  {stateLabel(s)}
                </MenuItem>
              ))}
            </TextField>
          </Grid>

          <Grid item xs={12} md={4}>
            <Autocomplete
              fullWidth
              options={beneCities}
              value={
                beneCities.find(
                  (c) => cityId(c) === String(form.beneficiary_city_id || "")
                ) || null
              }
              onChange={(_, val) =>
                setForm((prev) => ({
                  ...prev,
                  beneficiary_city_id: val ? cityId(val) : "",
                  beneficiary_addr_state_id: val
                    ? cityStateId(val)
                    : prev.beneficiary_addr_state_id,
                }))
              }
              getOptionLabel={(o) => (o ? cityLabel(o) : "")}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label={t("remesas.common.city")}
                  disabled={!form.beneficiary_nationality}
                />
              )}
            />
          </Grid>

          <Grid item xs={12}>
            <TextField
              fullWidth
              label={t("remesas.form.address")}
              value={form.beneficiary_address}
              onChange={handleChange("beneficiary_address")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              select
              fullWidth
              label={t("remesas.form.docType")}
              value={form.beneficiary_doc_type}
              onChange={handleChange("beneficiary_doc_type")}
            >
              {docTypeOptions.map((d) => (
                <MenuItem key={d.value} value={d.value}>
                  {t(`remesas.enums.docType.${d.value}`)}
                </MenuItem>
              ))}
            </TextField>
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label={t("remesas.form.docNumber")}
              value={form.beneficiary_doc_number}
              onChange={handleChange("beneficiary_doc_number")}
            />
          </Grid>

          <Grid item xs={12} md={2}>
            <TextField
              fullWidth
              type="date"
              label={t("remesas.form.docIssueDate")}
              InputLabelProps={{ shrink: true }}
              value={form.beneficiary_doc_issued_date || ""}
              onChange={handleChange("beneficiary_doc_issued_date")}
            />
          </Grid>

          <Grid item xs={12} md={2}>
            <TextField
              fullWidth
              type="date"
              label={t("remesas.form.docDueDate")}
              InputLabelProps={{ shrink: true }}
              value={form.beneficiary_doc_due_date || ""}
              onChange={handleChange("beneficiary_doc_due_date")}
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <Autocomplete
              fullWidth
              options={countries}
              value={doc_countryToOption(
                countries,
                form.beneficiary_doc_country
              )}
              onChange={(_, val) =>
                setForm((prev) => ({
                  ...prev,
                  beneficiary_doc_country: val ? getCountryCode(val) : "",
                }))
              }
              getOptionLabel={(o) => (o ? getCountryName(o) : "")}
              renderInput={(params) => (
                <TextField {...params} label={t("remesas.form.docCountry")} />
              )}
            />
          </Grid>
        </Grid>

        <Divider sx={{ my: 3 }} />

        <Stack direction="row" justifyContent="flex-end">
          <LoadingButton
            variant="contained"
            onClick={handleSubmit}
            loading={saving}
          >
            {t("remesas.actions.update")}
          </LoadingButton>
        </Stack>
      </Card>
    </Stack>
  );
}
