import { useState } from "react";
import { useSnackbar } from "notistack";
// @mui
import { Container, TextField, Stack } from "@mui/material";
import { LoadingButton } from "@mui/lab";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// hooks
import useSettings from "../../hooks/useSettings";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
import { getTicketTransactionId } from "../../redux/slices/ticket";
// sections
import Ticket from "../../sections/@dashboard/ticket/details/UpdateTicket";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";

export default function TicketEdit() {
  const { themeStretch } = useSettings();
  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();

  const [currentTicket, setCurrentTicket] = useState(null);
  const [ticketId, setTicketId] = useState("");
  const [loading, setLoading] = useState(false);

  const handleGetObject = async () => {
    if (!ticketId) return;

    setLoading(true);
    try {
      const result = await getTicketTransactionId(ticketId);
      setCurrentTicket(result);
    } catch (error) {
      console.error(error);
      const errorMessage = extractErrorMessage(error);
      enqueueSnackbar(errorMessage, { variant: "error" });
      setCurrentTicket(null);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Page title={`Ticket: ${t("p_ticket.editTicket")}`}>
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={t("p_ticket.editTicket")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            { name: t("p_ticket.ticket"), href: PATH_DASHBOARD.ticket.root },
            { name: currentTicket?.full_name || "" },
          ]}
        />

        <Stack
          direction={{ xs: "column", sm: "row" }}
          spacing={2}
          sx={{ mb: 4 }}
        >
          <TextField
            fullWidth
            label={t("p_ticket.enter_ticket_id")}
            value={ticketId}
            onChange={(e) => setTicketId(e.target.value)}
          />

          <LoadingButton
            variant="contained"
            color="primary"
            onClick={handleGetObject}
            loading={loading}
            sx={{ px: 4 }}
          >
            {t("p_ticket.search")}
          </LoadingButton>
        </Stack>

        {currentTicket && <Ticket ticket={currentTicket} />}
      </Container>
    </Page>
  );
}
